import React from 'react'
import ReactDOM from 'react-dom'
import './style/weather.css'

class Card extends React.Component {
    render() {
        return(
            <div className="weather-card">
                <div className="weather-icon">
                    <img src={require("./images/wicons/" + this.props.cond + ".svg")} alt="icon" />
                </div>
                <h1>{this.props.temp}ยบ</h1>
                <p>{this.props.dt}</p>
            </div>
        );
    }
}

function wIcon(cond){
    let result;
    if( cond >= 200 && cond < 300 ){
        result = "thunder";
    }else if(cond < 500){
        result = "rainy-4";
    }else if(cond < 600){
        result = "rainy-6";
    }else if(cond < 700){
        result = "snowy-5";
    }else if(cond < 800){
        result = "cloudy-day-1";
    }else if(cond === 800){
        result = "day";
    }else{
        result = "cloudy";
    }
    return result;
}

class FiveDay extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            weather: [],
            currentCity: '',
            currentCountry: '',
        };
    }

    componentDidUpdate(prevProps) {
        if (this.props.url !== prevProps.url) {
            const options = { weekday: 'short', month: 'short', day: '2-digit' };

            console.log(this.props.url);
            fetch(this.props.url)
            .then((response) => {
                if (!response.ok) throw Error(response.statusText);
                return response.json();
            })
            .then((data) => {
                console.log(data);
                let five = [];
                for(let i in data.list){
                    if(i % 8 === 3){
                        let one = {};
                        let current = data.list[i];
                        let dt = new Date(current.dt * 1000);
                        one["dt"] = dt.toLocaleString('en-US', options);
                        one["temp"] = Math.round(current.main.temp);
                        one["cond"] = wIcon(current.weather[0].id);
                        five.push(one);
                    }
                }
                console.log(five);
                this.setState({
                    currentCity: data.city.name,
                    currentCountry: data.city.country,
                    weather: five,
                });
    
    
            })
            .catch(error => console.log(error)); // eslint-disable-line no-console
        }
      }

    componentDidMount() {
        const options = { weekday: 'short', month: 'short', day: '2-digit' };

        console.log(this.props.url);
        fetch(this.props.url)
        .then((response) => {
            if (!response.ok) throw Error(response.statusText);
            return response.json();
        })
        .then((data) => {
            console.log(data);
            let five = [];
            for(let i in data.list){
                if(i % 8 === 3){
                    let one = {};
                    let current = data.list[i];
                    let dt = new Date(current.dt * 1000);
                    one["dt"] = dt.toLocaleString('en-US', options);
                    one["temp"] = Math.round(current.main.temp);
                    one["cond"] = wIcon(current.weather[0].id);
                    five.push(one);
                }
            }
            console.log(five);
            this.setState({
                currentCity: data.city.name,
                currentCountry: data.city.country,
                weather: five,
            });


        })
        .catch(error => console.log(error)); // eslint-disable-line no-console

    }

    render() {
        const cards = [];
        this.state.weather.forEach((day) => {
            cards.push(<Card dt={day.dt} temp={day.temp} cond={day.cond} />);
        });

        return(
            <div className="weather-large">
                <div className="weather-header">
                    <h1>{this.state.currentCity}, {this.state.currentCountry}</h1>
                </div>
                <div className="weather-wrapper">
                    {cards}
                </div>
            </div>
           
        );
    }
}



class Index extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            apiKey: "312b1bac995abc93b80f070e12bc0b95",
            setCity: "Ann Arbor",
        }
    }

    handleClick(e){
        console.log(e.target.value);
        this.setState({
            setCity: e.target.value,
        });
        return;
    }

    render(){
        return (
        <div>
            <div className="header-content">
                <nav className="header-nav-wrap">
                        <ul className="header-nav">
                            <li><button>My Weather</button></li>
                            <li><button onClick={(e) => this.handleClick(e)} value={"Ann Arbor"}>Ann Arbor</button></li>
                            <li><button onClick={(e) => this.handleClick(e)} value={"Beijing"}>Beijing</button></li>
                            <div className="search-container">
                                <form>
                                <input type="text" placeholder="Search.." name="search"></input>
                                <button type="submit"><i className="fa fa-search"></i></button>
                                </form>
                            </div>
                        </ul>
                </nav>
            </div>
            <FiveDay url={"https://api.openweathermap.org/data/2.5/forecast?units=metric&q="+this.state.setCity+"&appid="+this.state.apiKey} />
        </div>
        
        );
    }
}

ReactDOM.render(
    <Index />,
    document.getElementById('root')
);